import * as THREE from "three";

import Common from "@/graphics/Common";
import Device from "@/pure/Device";

// import Lighting from "@/graphics/components/Lighting";
import Atlas from "@/graphics/components/Atlas";
import Background from "@/graphics/components/Background";
import Circle from "@/graphics/components/Circle";

import Input from "@/graphics/Input";
import { easing } from "maath";

import gsap from "@gsap/shockingly";
import Draggable from "@gsap/shockingly/Draggable";

export default class Output {
  components = {};
  postProcess = false;
  proxy = document.createElement("div");
  variables = {
    rotationY: 6.28 - 1.3125,
    velocity: 0.0
  };
  startProgress = 0;
  progressWrap = gsap.utils.wrap(0, 1);
  spin = gsap.fromTo(this.variables, {
    rotationY: 6.28
  }, {
    paused: true,
    rotationY: 0,
    duration: 20,
    ease: "none",
    repeat: -1,
  });

  constructor() {
    this.init();
  }

  init() {
    let { spin, startProgress, progressWrap, variables } = this;

    // this.components.lighting = new Lighting();
    this.components.atlas = new Atlas();
    this.components.background = new Background({
      el: document.body.querySelector(".corps")
    });
    this.components.circle = new Circle({
      diameter: 7,
      scale: 1,
      circle: 0,
      opacity: 0.1
    });

    this.page = new THREE.Group();
    this.page.matrixAutoUpdate = false;

    this.page.add(this.components.atlas);
    this.page.add(this.components.background);
    this.components.atlas.add(this.components.circle);
    
    Common.scene.add(this.page);

    this.target = new THREE.WebGLRenderTarget(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio, {
      minFilter: THREE.LinearFilter,
      magFilter: THREE.LinearFilter,
      type: THREE.HalfFloatType,
    })
    spin.progress(1.3125 / 6.28);
    const dragDistancePerRotation = 3000;
    
    const updateRotation = () => {
      let p = startProgress + (this.drag[0].startX - this.drag[0].x) / dragDistancePerRotation;
      spin.progress(progressWrap(p));
      this.components.atlas.sphere.rotation.y = variables.rotationY;
      this.components.atlas.sphere.updateMatrix();
    }
    this.drag = Draggable.create(this.proxy, {
      trigger: document.body,
      type: "x",
      inertia: true,
      allowNativeTouchScrolling: true,
      onPress() {
        gsap.killTweensOf(spin);
        spin.timeScale(0);
        startProgress = spin.progress();
      },
      onDrag: updateRotation,
      onThrowUpdate: updateRotation,
    });
  }

  updateY(y) {
    const p = y.end / Device.viewport.height;
    const d = p *  Device.fov.height;
    this.page.position.y = y.progress * d;
    this.page.updateMatrix();
  }

  updateVelocity({ velocity }) {
    const { variables } = this;
    if (this.velocityTween) {
      this.velocityTween.kill();
    }
    this.velocityTween = gsap.timeline({
      onUpdate: () => {
        this.components.background.material.userData.velocity.value = variables.velocity;
      }
    })
    const f = Math.min(1.0, Math.abs(velocity) / Device.viewport.height);
    const v = f * -Math.sign(velocity);

    this.velocityTween.set(variables, {
      velocity: v,
      duration: 0.05,
      ease: 'sine.in'
    }, 'a')
    this.velocityTween.to(variables, {
      velocity: 0,
      duration: 0.2 * f,
      ease: 'expo.out'
    }, 'a+=0.05')
  }

  render(t, delta) {
    if (this.components.atlas) {
      easing.damp3(
        this.components.atlas.position,
        [
          this.components.atlas.position.x,
          this.components.atlas.position.y,
          (1.0 - Math.cos(Input.coords.x)) * 2.25
        ],
        0.2,
        delta / 1000,
      )

      easing.damp3(
        this.components.atlas.rotation,
        [
          Input.coords.y * 3.14 * 0.0314 * 0.9,
          Input.coords.x  * 3.14 * 0.0314 * 1.4,
          0,
        ],
        0.2,
        delta / 1000,
      );

      Object.keys(this.components).forEach(_ => {
        this.components[_].render(t);
      });
      
      this.components.atlas.glyph.visible = false;
      this.components.atlas.glyph2.visible = false;

      Common.renderer.setRenderTarget(this.target);
      Common.render();
      
      this.target.texture.colorSpace = THREE.LinearSRGBColorSpace;
      this.components.atlas.glyph.children[0].material.uniforms.uTexture.value = this.target.texture;
      this.components.atlas.glyph2.children[0].material.uniforms.uTexture.value = this.target.texture;
      
      this.components.atlas.glyph.visible = true;
      this.components.atlas.glyph2.visible = true;
    }

    Common.renderer.setRenderTarget(null);
    Common.render();
  }

  dispose() {
    this.drag[0].kill();
    Object.keys(this.components).forEach(_ => {
      this.page.remove(this.components[_]);
      this.components[_].dispose();
      delete this.components[_];
    });
    
    this.target.dispose();
    Common.scene.remove(this.page);
    this.page = null;
    
  }

  resize() {
    Object.keys(this.components).forEach(_ => {
      this.components[_].resize();
    });
    this.target.setSize(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);

  }
}
