import {
  Mesh,
  PlaneGeometry,
  MeshBasicMaterial,
  // ShaderChunk
} from 'three';

import Device from "@/pure/Device";

export default class Background extends Mesh {
  constructor(params = {}) {
    const material = new MeshBasicMaterial({
      color: params.color || 0xffffff,
      // wireframe: true,
      userData: {
        velocity: { value: 0.0 }
      },
    });
    
    super(new PlaneGeometry(1, 1, 100), material);
    material.onBeforeCompile = shader => {
      shader.uniforms.velocity = material.userData.velocity
   
      shader.vertexShader = 'uniform float velocity;\n' + shader.vertexShader
      shader.vertexShader = 
        shader.vertexShader.replace(
          '#include <begin_vertex>',
          `
            vec3 transformed = vec3( position );
            transformed.y += cos(position.x * 3.14) * velocity * 0.025;
          `
        );
    };
    this.el = params.el;
    this.matrixAutoUpdate = false;
  }
  render() {
  }
  dispose() {
  }
  resize() {
    const { viewport, fov } = Device;
    // console.log("resize", this.el.offsetTop / viewport.height, this);
    const ratio = (this.el.offsetTop - viewport.height * 0.5 + this.el.offsetHeight * 0.5) / viewport.height;
    const rs = this.el.offsetHeight / viewport.height;
    // this.position.y = -ratio * fov.height + fov.height * 0.25;
    this.position.y = -ratio * fov.height;
    this.scale.x = fov.width;
    this.scale.y = rs * fov.height;
    this.updateMatrix();
  }
}
