<template>
  <!-- <div class="scenic-world">
    <canvas ref="canvas" />
  </div> -->
  <HomeView />
</template>

<script>
import { onMounted, onUnmounted } from 'vue'
import HomeView from "@/views/HomeView.vue"
import App from "@/graphics/index";
import ScrollTrigger from "@gsap/shockingly/ScrollTrigger";

export default {
  components: {HomeView},
  setup() {
    let app = null;
    let st = null;
    
    onMounted(() => {
      app = new App();
      st = new ScrollTrigger({
        trigger: document.body.querySelector("#app"),
        scrub: true,
        start: "top top-=1px",
        end: "bottom bottom+=1px",
        onUpdate: (self) => {
          const { end, progress } = self;
          app.output.updateY({
            end, progress
          });
          app.output.updateVelocity({
            velocity: self.getVelocity()
          });
        }
      });
    });
    onUnmounted(() => {
      app.dispose();
      st.kill();
    });
  },
}
</script>


