import {
  SphereGeometry,
  Mesh,
  MeshStandardMaterial as MeshPhysicalMaterial,
  TextureLoader,
  Color,
  CanvasTexture,
  RepeatWrapping,
  Vector2,
  Vector3,
  UVMapping,
  Group,
  EquirectangularReflectionMapping,
} from "three";

import AlphaFragment from "./alpha.fragment.glsl";

import Device from "@/pure/Device";

import { RGBELoader } from 'three/addons/loaders/RGBELoader.js';

import { FlakesTexture } from 'three/addons/textures/FlakesTexture.js';

import { Glyph } from 'three-glyph';

import font from '@/assets/fonts/Love.json';
import PPNeueMontrealMedium from '@/assets/fonts/PPNeueMontreal-Medium.json';

import gsap from '@gsap/shockingly';

export default class Atlas extends Group {
  constructor() {
    super();

    // this.matrixAutoUpdate = false;
    // this.geometry = new SphereGeometry(1, 1, 1);
    this.geometry = new SphereGeometry(1, 64, 64);

    this.init();
    const textureLoader = new TextureLoader();
    const worldMap = textureLoader.load( "/assets/map.png" );
    const alphaMap = textureLoader.load( "/assets/transition4.png" );
      
    this.material = new MeshPhysicalMaterial({
      // color: new Color(0xa58946),
      // color: new Color(0xffa500),
      color: new Color(0xDAA520),
      metalness: 1.0,
      roughness: 0.15,
      roughnessMap: worldMap,
      normalMap: null,
      normalScale: new Vector2(0.1, 0.1),
      envMap: this.textureCube,
      map: worldMap,
      alphaMap,
      transparent: true,
      userData: {
        transition: { value: 1.0 }
      },
      
    });

    this.material.normalMap = new CanvasTexture(new FlakesTexture(), UVMapping, RepeatWrapping, RepeatWrapping);
    this.material.normalMap.repeat.set(40, 40);


    this.material.onBeforeCompile = shader => {
      shader.uniforms.transition = this.material.userData.transition
   
      shader.fragmentShader = 'uniform float transition;\n' + shader.fragmentShader
      shader.fragmentShader = 
        shader.fragmentShader.replace(
          '#include <alphamap_fragment>',
          AlphaFragment
        )
    };

    this.sphere = new Mesh( this.geometry, this.material );
    this.sphere.matrixAutoUpdate = false;
    this.sphere.rotation.y = 6.28 - 1.3125;
    this.sphere.rotation.x = .875;
    this.sphere.scale.set(.9 * 3.0, .9 * 3.0, .9 * 3.0);
    this.sphere.updateMatrix();
    this.add( this.sphere );



    const map = textureLoader.load( "/assets/Love.png" );

    const addons = {
      progress: true,
      shaderChunks: {
        'position_pars_vertex': `
          // https://github.com/glslify/glsl-easings/
          float quadraticOut(float t) {
            return -t * (t - 2.0);
          }
          float cubicOut(float t) {
            float f = t - 1.0;
            return f * f * f + 1.0;
          }
          uniform mat4 modelMatrix;
          uniform mat3 normalMatrix;
          uniform vec3 cameraPosition;

          in vec3 normal;

          out vec3 worldNormal;
          out vec3 eyeVector;
        `,
        'transformed_vertex': `
          vec4 worldPos = modelMatrix * vec4(position.xyz, 1.0);

          transformed.x += 260.0 * 0.5 * (1.0 - quadraticOut(vProgress));
          transformed.z += 260.0 * (1.0 - cubicOut(vProgress));

          vec3 transformedNormal = normalMatrix * normal;
          worldNormal = normalize(transformedNormal);

          eyeVector = normalize(worldPos.xyz - cameraPosition);
        `,
        'color_pars_fragment':  `
          uniform vec3 color;
          uniform vec2 resolution;
          uniform vec3 cameraPosition;
          uniform sampler2D uTexture;

          uniform vec3 uLight;
          uniform float uShininess;
          uniform float uDiffuseness;

          uniform float uIorR;
          uniform float uIorY;
          uniform float uIorG;
          uniform float uIorC;
          uniform float uIorB;
          uniform float uIorP;

          uniform float uRefractPower;
          uniform float uChromaticAberration;
          uniform float uSaturation;


          in vec3 worldNormal;
          in vec3 eyeVector;
          
          // https://github.com/mrdoob/three.js/blob/205e345b4f48f8b560b3b35391fd914cba1997a0/src/renderers/shaders/ShaderChunk/encodings_pars_fragment.glsl.js#L20-L22
          vec4 LinearTosRGB( in vec4 value ) {
            return vec4( mix( pow( value.rgb, vec3( 0.41666 ) ) * 1.055 - vec3( 0.055 ), value.rgb * 12.92, vec3( lessThanEqual( value.rgb, vec3( 0.0031308 ) ) ) ), value.a );
          }

          vec3 sat(vec3 rgb, float adjustment) {
            const vec3 W = vec3(0.2125, 0.7154, 0.0721);
            vec3 intensity = vec3(dot(rgb, W));
            return mix(intensity, rgb, adjustment);
          }

          float specular(vec3 light, float shininess, float diffuseness) {
            vec3 normal = worldNormal;
            vec3 lightVector = normalize(-light);
            vec3 halfVector = normalize(eyeVector + lightVector);

            float NdotL = dot(normal, lightVector);
            float NdotH =  dot(normal, halfVector);
            float kDiffuse = max(0.0, NdotL);
            float NdotH2 = NdotH * NdotH;

            float kSpecular = pow(NdotH2, shininess);
            return  kSpecular + kDiffuse * diffuseness;
          }
          const int LOOP = 16;
        `,
        'color_fragment':  `
          float iorRatio = 1.0/1.31;
          vec2 uv = gl_FragCoord.xy / resolution.xy;
          vec3 normal = worldNormal;

          vec3 color = vec3(0.0);

          for ( int i = 0; i < LOOP; i ++ ) {
            float slide = float(i) / float(LOOP) * 0.1;
        
            vec3 refractVecR = refract(eyeVector, normal,(1.0/uIorR));
            vec3 refractVecY = refract(eyeVector, normal, (1.0/uIorY));
            vec3 refractVecG = refract(eyeVector, normal, (1.0/uIorG));
            vec3 refractVecC = refract(eyeVector, normal, (1.0/uIorC));
            vec3 refractVecB = refract(eyeVector, normal, (1.0/uIorB));
            vec3 refractVecP = refract(eyeVector, normal, (1.0/uIorP));
        
            float r = texture(uTexture, uv + refractVecR.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).x * 0.5;
        
            float y = (texture(uTexture, uv + refractVecY.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).x * 2.0 +
                        texture(uTexture, uv + refractVecY.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).y * 2.0 -
                        texture(uTexture, uv + refractVecY.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).z) / 6.0;
        
            float g = texture(uTexture, uv + refractVecG.xy * (uRefractPower + slide * 2.0) * uChromaticAberration).y * 0.5;
        
            float c = (texture(uTexture, uv + refractVecC.xy * (uRefractPower + slide * 2.5) * uChromaticAberration).y * 2.0 +
                        texture(uTexture, uv + refractVecC.xy * (uRefractPower + slide * 2.5) * uChromaticAberration).z * 2.0 -
                        texture(uTexture, uv + refractVecC.xy * (uRefractPower + slide * 2.5) * uChromaticAberration).x) / 6.0;
                  
            float b = texture(uTexture, uv + refractVecB.xy * (uRefractPower + slide * 3.0) * uChromaticAberration).z * 0.5;
        
            float p = (texture(uTexture, uv + refractVecP.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).z * 2.0 +
                        texture(uTexture, uv + refractVecP.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).x * 2.0 -
                        texture(uTexture, uv + refractVecP.xy * (uRefractPower + slide * 1.0) * uChromaticAberration).y) / 6.0;
        
            float R = r + (2.0*p + 2.0*y - c)/3.0;
            float G = g + (2.0*y + 2.0*c - p)/3.0;
            float B = b + (2.0*c + 2.0*p - y)/3.0;
        
            color.r += R;
            color.g += G;
            color.b += B;
        
            // color = sat(color, uSaturation);
          }


          // vec3 refractVec = refract(eyeVector, normal, iorRatio);
          // diffuseColor = texture(uTexture, uv).rgb;
          // diffuseColor = vec3(0.5, 0.5, 1.0) - texture(uTexture, uv + refractVec.xy).rgb;
          // diffuseColor = LinearTosRGB(texture(uTexture, uv + refractVec.xy)).rgb;
          // diffuseColor = LinearTosRGB(vec4(color, 1.0)).rgb;
          diffuseColor = color;

          float specularLight = specular(uLight, uShininess, uDiffuseness);
          diffuseColor += specularLight;
        `,
        'alpha_fragment':  `alpha *= vProgress * opacity;`,
        // 'alpha_fragment':  `alpha = 1.0;`,
      }
    }
    this.glyph = new Glyph({
      text: "RÉMI TRAN",
      font,
      map,
      color: new Color(0x030c1e),
      letterSpacing: 10,
      addons
    });
    
    this.glyph.matrixAutoUpdate = false;
    this.glyph.mesh.matrixAutoUpdate = false;
    this.glyph.scale.set(0.0075, 0.0075, 0.0075)
    this.glyph.center();
    this.glyph.children[0].geometry.computeVertexNormals();
    this.glyph.position.z = 2.75;

    this.glyph.material.uniforms.progress.value = 0;
    this.glyph.material.uniforms.uTexture = {
      value: null
    };
    this.glyph.material.uniforms.resolution = {
      value: new Vector2(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio)
    };
    this.glyph.material.uniforms.uLight = {
      value: new Vector3(-0.5, 4.0, 10.0),
    };
    this.glyph.material.uniforms.uDiffuseness = {
      value: 0.2,
    };
    this.glyph.material.uniforms.uShininess = {
      value: 40,
    };

    this.glyph.material.uniforms.uIorR = { value: 1.15 };
    this.glyph.material.uniforms.uIorY = { value: 1.16 };
    this.glyph.material.uniforms.uIorG = { value: 1.18 };
    this.glyph.material.uniforms.uIorC = { value: 1.22 };
    this.glyph.material.uniforms.uIorB = { value: 1.22 };
    this.glyph.material.uniforms.uIorP = { value: 1.22 };

    this.glyph.material.uniforms.uSaturation = { value: 1.14 };
    this.glyph.material.uniforms.uChromaticAberration = { value: 0.5 };
    this.glyph.material.uniforms.uRefractPower = { value: 0.25 };

    // const helper = new VertexNormalsHelper( this.glyph.children[0], 1, 0xff0000 );
    this.glyph.updateMatrix();
    this.glyph.mesh.updateMatrix();
    this.add(this.glyph);
    // scene.add(helper);



    this.glyph2 = new Glyph({
      // text: "CODER - CARTOGRAPHER",
      text: "CODER",
      font: PPNeueMontrealMedium,
      map,
      color: new Color(0xffffff),
      letterSpacing: 20,
      addons
    });

    this.glyph2.matrixAutoUpdate = false;
    this.glyph2.mesh.matrixAutoUpdate = false;
    this.glyph2.scale.set(0.0075, 0.0075, 0.0075)
    this.glyph2.position.z = 2.8;
    this.glyph2.position.y = -1.025;
    this.glyph2.center();
    this.glyph2.children[0].geometry.computeVertexNormals();
    this.glyph2.updateMatrix();
    this.glyph2.mesh.updateMatrix();
    this.add(this.glyph2);

    // scene.add( plane );
    // const helper2 = new VertexNormalsHelper( plane, 1, 0xff0000 );
    // scene.add( helper2 );
  }
  init() {
    this.textureCube = new RGBELoader()
      .setPath( '/assets/' )
      // .load( 'potsdamer_platz_1k.hdr',  ( texture ) => {
      .load( 'potsdamer_platz_1k_11zon.hdr',  ( texture ) => {
      // .load( 'Contrast-Black-Jewelry-HDRI-Vol2.hdr',  ( texture ) => {
        // rotating envMap waiting r162
        texture.mapping = EquirectangularReflectionMapping;
        // console.log(texture);
        // texture.matrix.setUvTransform(0, 0, 0, 0, 3.14, 0, 0);
        // texture.needsUpdate = true;
        const lerp = {
          x: 0,
          y: 0,
          z: 1
        }
        const tl = new gsap.timeline();
        tl.to(lerp, {
          z: 0,
          // repeat: -1,
          duration: 2.5,
          yoyo: true,
          ease: "linear",
          onUpdate: () => {
            this.material.userData.transition.value = lerp.z;
          }
        }, 'a')
        tl.to(lerp, {
          x: 1,
          duration: 1.5,
          ease: "linear",
          onUpdate: () => {
            this.glyph.material.uniforms.progress.value = lerp.x;
          }
        },'a+=1.5')
        tl.to(lerp, {
          y: 1,
          duration: 1,
          ease: "linear",
          onUpdate: () => {
            this.glyph2.material.uniforms.progress.value = lerp.y;
          }
        },'a+=2')
      } );
    
  }
  render() {
    // this.sphere.rotation.y += 0.001
    // this.glyph.material.uniforms.progress.value += 0.01;

    // Common.scene.environmentRotation.x += texture;
  }
  dispose() {
    this.remove(this.sphere);
    this.remove(this.glyph);
    this.remove(this.glyph2);
    this.sphere.geometry.dispose();
    this.sphere.material.dispose();
    this.glyph.geometry.dispose();
    this.glyph.material.dispose();
    this.glyph2.geometry.dispose();
    this.glyph2.material.dispose();
  }
  resize() {
    this.glyph.material.uniforms.resolution.value.set(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);
    this.glyph2.material.uniforms.resolution.value.set(Device.viewport.width * Device.pixelRatio, Device.viewport.height * Device.pixelRatio);
  }
}
